.First {
  max-width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: 49rem;
  position: relative;
  transition: background-image 3s ease-in-out;
  transition-delay: 0.2s;

  &-content {
    display: grid;
    grid-template-rows: 40% 10% 40%;
  }

  &__info {
    padding-top: 122px;
    display: grid;
    justify-content: start;
    margin-left: 16vw;
  }

  &__logo {
    width: 841px;
  }

  &__title {
    color: #FFF;
    font-family: 'IBM Plex Mono', monospace;
    font-size: 24.542px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-transform: uppercase;
  }

  &__projects {
    padding-top: 186px;
    padding-bottom: 108px;
    display: flex;
    align-items: center;
    justify-content: center;

    &__title {
      transform: rotate(-90deg);
      color: #FFF;
      font-family: 'Kyiv Region', sans-serif;
      font-size: 16px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
      text-transform: uppercase;
    }

    &__wrapper {
      display: flex;
      gap: 25px;
    }

    &__block {
      position: relative;
      display: flex;
      border: 3px solid #FFF;

      &:hover .First__projects__img {
        filter: grayscale(0%);
      }
    }

    &__img {
      width: 166px;
      height: 169px;
      object-fit: cover;
      filter: grayscale(100%);
      transition: filter 0.3s ease;
    }

    &--container {
      position: absolute;
      width: 15%;
      display: grid;
      grid-template-columns: 0.25fr 1fr;
      grid-template-rows: 0.35fr 1fr;
      transition: transform 0.25s;
      margin: 0 0.4rem;

    }

    &--number {
      text-align: center;
      color: white;
      width: 1.25rem;
      height: 1.25rem;
      font-family: 'IBM Plex Mono', monospace;
      font-size: 11px;
      font-weight: 600;
      line-height: 19px;
      background-color: #BF1616;
      border-radius: 50%;
      grid-row: 1/3;
      align-self: center;
      justify-self: end;
      margin: 0 0.25rem 0.5rem 0.25rem;
    }

    &--title {
      font-family: 'IBM Plex Mono', monospace;
      font-size: 12px;
      font-weight: 600;
      line-height: 11px;
      text-align: center;
      text-transform: uppercase;
      color: white;
      margin: 1rem 0 0 0;
      justify-self: center;
      align-self: center;
      width: 8rem;
    }

    &--square {
      font-family: 'IBM Plex Mono', monospace;
      font-size: 9px;
      font-weight: 400;
      line-height: 9px;
      text-align: center;
      text-transform: uppercase;
      color: white;
      margin: 0;
      width: 5rem;
      grid-column-start: 2;
      justify-self: center;
    }

  }
}

.dots {
  justify-self: end;
  position: relative;
  top: 1rem;
  right: 3rem;
  transform: translateX(0) rotate(90deg);
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 45px;
  @media screen and (max-width: 1060px) {
    transform: translateX(0) rotate(90deg);
  }
  @media screen and (max-width: 1023px){
    display: none;
  }
}

.dotes {
  width: 11px;
  height: 11px;
  background-color: #fff;
  border: 2px solid #000;
  border-radius: 50%;
  margin: 0 5px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease, width 0.3s ease, height 0.3s ease;
  z-index: 1;
}

.dotes.active {
  background-color: #969696;
  width: 11px;
  height: 45px;
  border-radius: 31px;
  transform: rotate(90deg);
  margin: 0 1rem;
  z-index: 2;
}

@media (max-width: 1023px) {

  .First {

    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    height: auto;
    position: relative;
    padding-bottom: 1.5rem;

    &__projects {
      padding: 0.5rem 1.5rem;
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: auto;
      row-gap: 3rem;

      &--container {
        width: 10rem;
        margin: 0 1.5rem;
      }

      &__wrapper {
        flex-wrap: wrap;
        justify-content: center;
        gap: 2rem;
      }

      &__img {
        width: 16rem;
        height: 16rem;
      }

      &__title {
        justify-self: center;
        transform: rotate(0deg);
        margin: 0;
      }
    }
  }

  .First__info {
    padding: 5rem 3rem 0;
    display: flex;
    flex-direction: column;
  }

  .First__logo {

    width: 100%;
  }

  .First__title {
    font-size: 24px;
    font-weight: 400;
    line-height: 29px;
  }
  .dots {
    transform: translate(0, 0);
    position: relative;
    bottom: 0;
    right: 0;
    grid-row: 3;
  }
}

@media (max-width: 480px) {
  .First {
    &__logo {
      width: 100%;

    }

    &__projects {
      padding: 1rem;
    }
  }

  .First__info {
    padding: 2rem 2rem 0;
  }
  .First__title {
    font-size: 13px;
    line-height: 15px;
  }
  .First__projects__wrapper {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    gap: 2rem;
  }
  .First__projects__block {
    width: 10.5rem;
    height: 10.5rem;
    margin-right: 0;
  }
  .First__projects__img {
    height: 10rem;
    width: 10.5rem;
  }
  .First__projects--container {
    margin: 0;
    display: grid;
    transform: none;
  }
  .First__projects--number {
    width: 1rem;
    height: 1rem;
  }
}
